import { Visibility, VisibilityOff } from '@mui/icons-material';
import {
  Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Modal,
  Select,
  TextField,
  Typography,
} from '@mui/material';
import React, { useState } from 'react';
import { auth } from '../../api/checkCookie';

//37a87a01-2625-4b4b-86b0-48807eebefaf

function Login(openModal: boolean) {
  const [open, setOpen] = useState<boolean>(openModal);
  const [campus, setCampus] = useState(1);
  const [showPassword, setShowPassword] = useState<boolean>(true);
  const [password, setPassword] = useState<string>(
    '37a87a01-2625-4b4b-86b0-48807eebefaf'
  );

  // const handleClose = () => {
  //   setOpen(false);
  // };

  const handleCampus = (value: any) => {
    setCampus(value);
  };

  const handleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleSubmit = () => {
    auth(password);
    localStorage.setItem('siteName', campus ? 'Hobart' : 'Glenorchy');
    console.log(password);
    setOpen(false);
    setPassword('');
  };

  return (
    <Modal open={open}>
      <Box
        sx={{
          width: '50%',
          height: 400,
          textAlign: 'center',
          margin: 'auto',
          backgroundColor: 'white',
          padding: 5,
          marginTop: 6,
        }}
      >
        <Typography variant="h4">Login (Api Key)</Typography>
        <FormControl fullWidth sx={{ marginTop: 4, width: '50%' }}>
          <InputLabel>Select Campus</InputLabel>
          <Select
            required
            label="Select Campus"
            value={campus}
            onChange={(e) => handleCampus(e.target.value)}
          >
            <MenuItem value={1}>Hobart</MenuItem>
            <MenuItem value={2}>Glenorchy</MenuItem>
          </Select>
          <TextField
            required
            label="API Key"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            type={showPassword ? 'password' : 'text'}
            sx={{ marginTop: 4 }}
            InputProps={{
              endAdornment: (
                <Button onClick={handleShowPassword}>
                  {showPassword ? <VisibilityOff /> : <Visibility />}
                </Button>
              ),
            }}
          />

          <Button
            variant="contained"
            sx={{ width: '50%', margin: 'auto', marginTop: 4 }}
            type="submit"
            onClick={handleSubmit}
          >
            Login
          </Button>
        </FormControl>
      </Box>
    </Modal>
  );
}

export default Login;
